import React, { useState, Fragment } from 'react';
import { useCookies } from 'react-cookie';
import ReserveModal from '../Modal/ReserveModal'

function Link(props) {
  const [show, setShow] = useState(false)
  const [cookies, setCookie, removeCookie] = useCookies(['directReserve']);

  const handleClose = () => setShow(false);
  const handleShow = (direct) => {
    if (direct === "true") {
      window.location.href = "https://reserva.be/lilystransupport_webinar2020"
    } else {
      setShow(true );
    }
  }
  const handleCheck = (event) => {
    console.log(cookies);
    
    if (event.target.checked) {
      setCookie('directReserve',"true")
    } else {
      removeCookie('directReserve')
    }
  }
  

  return (
    <Fragment>
      <button type="button" className="reserve-link centerlink" onClick={() => handleShow(cookies.directReserve)}><span>{props.children}</span></button>
      <ReserveModal
        show={show}
        handleClose={handleClose}
        handleCheck={handleCheck}
        directReserve={cookies.directReserve}
      />
    </Fragment>
  );
}

export default Link;
