import React from 'react'
import { Modal, Button } from 'react-bootstrap';
import { Link as RouterLink } from 'react-router-dom';

export default function ReserveModal(props) {
  const { show, handleClose, handleCheck, directReserve } = props
  return (
    <Modal
      show={show}
      onHide={handleClose}
      size="md"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          チケットの購入はお済みですか？
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>
          まだチケットを購入されていないお客様は，先に<RouterLink to="/tickets"　>こちら</RouterLink>からお申し込みください。
        </p>
        <input type="checkbox" id="direct-chekbox" value={directReserve} onChange={handleCheck} />
        <label htmlFor="direct-chekbox">今後この画面を表示しない</label>
      </Modal.Body>
      <Modal.Footer>
        <Button href="https://reserva.be/lilystransupport_webinar2020">予約を続ける</Button>
      </Modal.Footer>
    </Modal>
  )
}
